      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"position1"</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:point.sync</span>=<span class="hljs-string">"point1"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-point</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"point1"</span> <span class="hljs-attr">:color</span>=<span class="hljs-string">"color1"</span> <span class="hljs-attr">:pixel-size</span>=<span class="hljs-string">"8"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-point</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
