        <span class="hljs-attr">color1</span>: {},
        <span class="hljs-attr">position1</span>: { <span class="hljs-attr">lng</span>: -<span class="hljs-number">75.59777</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.03883</span> },

        <span class="hljs-attr">color2</span>: {},
        <span class="hljs-attr">position2</span>: { <span class="hljs-attr">lng</span>: -<span class="hljs-number">80.5</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">35.14</span> },

